// единицы измерения
$emSize: 1em;
$emSizeDouble: $emSize * 2;
$emSizeText: $emSize * 1.6;
$emSizeHeader: $emSize * 1.2;
$emSizePart: $emSize * 0.7;
$emSizeHalf: $emSize * 0.5;
$emSizeUn3: $emSize * 0.3;
$emSizeUn2: $emSize * 0.2;

// шрифты
$fontFamily: Verdana, sans-serif;
$fontSize: 14px;
$fontSizeH1: 38px;

$unit: $emSize / 14;

// цвета
$colorWhite: #fff;
$colorBlack: #000;
$colorMiddleBlack: #4d4d4d;
$colorDark: #364657;
$colorLight: #e5e6e9;
$colorMiddleLight: #b4b4b4;
$colorBlue: #1fb6ff;
$colorDarkBlue: #0077d3;


$colorMiddleDark: #666;
$colorMiddle: #999;
$colorYellow: #ff0;
$colorRed: #ff000c;

$colorDefault: #fafafb;
$colorPrimary: #0295d5;
$colorSucces: #78BE30;
$colorInfo: #74BBFF;
$colorWarning: #4ADB4A;
$colorDanger: #EB4F1F;

$rgbaDark: rgba($colorBlack, 0.3);

// текст
$colorText: $colorMiddleBlack;
$textShadowDark: 0 1px 2px $rgbaDark;

// ссылки
$colorLink: #1fb6ff;
$colorLinkVisited: #364657;
$colorLinkHover: #c6c6c6;
$colorActive: $colorText;

// border
$borderRadius: $emSize * 0.25;
$borderColor: $colorMiddleLight;
$border: 1px solid $borderColor;
$borderTableColor: $colorMiddleDark;
$borderTable: 1px solid $borderTableColor;

// футер

$footerSize: 325px;

// миксины
@mixin HVcenter($w, $h) {
	display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    max-width: $w;
    max-height: $h;
    margin: auto;
}